import React from 'react'
import Link from 'gatsby-link'

const Facilities = () => (
  <div>
    <section className="nkc-banner hidden-print"></section>
    <main className="nkc-container clearfix">
      <section className="breadcumb hidden-print">
        <div className="container clearfix">
          <div className="col-lg-full col-md-full col-sm-full">
            <p><Link to="/">Home</Link> &rarr; Facilities</p>
          </div>
        </div>
      </section>
      <div className="clearfix">
        <div className="col-lg-xlarge col-md-xlarge col-sm-full col-print-full clearfix">
          <div className="nkc-container__content clearfix">
            <h1 className="nkc-page-title">Facilities</h1>
            <h2 id="campus">Campus</h2>
            <p>With a spacious playground, North Kamrup College comprise a land holding of 33 Bigas. A large number of valuable trees like Sishu have been growing in and around the college campus. In collaboration with the Department of Social Forestry, Govt. of Assam, seedlings of valuable trees like Saal, Nahar, Teak, Sishu, Gomary, etc. are being produced in 5 Bigas of college land which are likely to help in creating environmental awareness among the students.</p>
            <h3>College Canteen</h3>
            <p>For refreshment of the students, teachers and employees of the college a well managed canteen has been rendering services in the college campus.</p>
            <center className="hidden-sm">· · ·</center>
            <h2 id="library">Library</h2>
            <p>The library of the college has a collection of 10,500 books, 5 journals, magazines and newspapers. It also maintains Book bank provisions for financially weak students of TDC level.</p>
            <p>The special attractions of the library is—</p>
            <ul>
              <li>Important Dictionaries</li>
              <li>Reference books</li>
              <li>Documents on North East India series</li>
              <li>Asomiya Biswakosh</li>
              <li>Encyclopedia of North East</li>
              <li>Encyclopedia of Guidance and Counseling</li>
              <li>Woman's Encyclopedia</li>
              <li>Books for Research work and competitive examinations</li>
              <li>Reading room</li>
              <li>Catalogue system</li>
              <li>Electricity, urinal, drinking water, etc.</li>
            </ul>
            <p></p>
            <center className="hidden-sm">· · ·</center>
            <h2 id="scholarships">Scholarships</h2>
            <p>The students may avail the following types of scholarship on the basis of merit by fulfilling the requisite conditions—</p>
            <ul>
              <li>Central National scholarship</li>
              <li>National merit scholarship for children of school teachers</li>
              <li>SC/ST/OBC/MOBC scholarship</li>
              <li>Special scholarship scheme (ISHAN UDAY) from UGC for meritorious students of Degree course.</li>
            </ul>
            <p></p>
            <center className="hidden-sm">· · ·</center>
            <h2 id="reprographic-facilities">Reprographic Facilities</h2>
            <p>Available reprographic facilities are there in the college with concession for students.</p>
            <center className="hidden-sm">· · ·</center>
            <h2 id="associations-and-clubs">Associations &amp; Clubs</h2>
            <h3>Students' Association</h3>
            <p>The college bears the tradition of democratic representation of students through annual election. The teachers render their services in guiding the students union with a sense of responsibility as In-charge in different portfolios. Normally the activities of the students union concentrate on holding Fresher's Social, annual College Week, social service, regular sports and gymnastics practices, publication of wall magazine and the annual College magazine, etc.</p>
            <h3>Internal Quality Assurance Cell (I. Q. A. C.)</h3>
            <p>The Internal Quality Assurance Cell evaluates the quality status of students on regular basis and takes measures to enhance the quality of teaching learning process.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Mr. P. K. Das</li>
              <li>Assistant Co-ordinator : Mr. P. Kalita</li>
            </ul>
            <p></p>
            <h3>Health Club</h3>
            <p>North Kamrup College Health Club is formed in 2013-14 with a view to conduct health awareness programs, health check-up camp, etc. not only in college premises but in outside in the locality too.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Mr. B. Goswami</li>
            </ul>
            <p></p>
            <h3>Grievance Redressal Cell</h3>
            <p>This cell looks for any kind of grievences of the student community of the college and efforts are taken to redress their grievences / complaints and control other unsocial and evil deeds within the college campus which may vitiate the academic environment.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Mr. D. K. Lahkar</li>
            </ul>
            <p></p>
            <h3>Cultural Society</h3>
            <p>To desseminate the talents of the students in music, dance and dramatic performance the cultural society of the college plans to organise workshops on dance, modern and folk songs with experts from different fields.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Dr. A. Das</li>
            </ul>
            <p></p>
            <h3>Anti Ragging Cell</h3>
            <p>As per direction of the Supreme Court of India, an anti ragging cell has been formed to monitor and control ragging in the college.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Mr. H. Das</li>
              <li>Assistant Co-ordinator : Mr. S. R. Dev Choudhury</li>
            </ul>
            <p></p>
            <h3>Information &amp; Career Guidance Cell</h3>
            <p>This cell helps the students providing information about the career development and employment. Special class, seminars and career counselling had been done by experts from different fields.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Dr. A. Das</li>
            </ul>
            <p></p>
            <h3>Women's Forum</h3>
            <p>The Women's Forum formed in 2012-13 aims to empower the poor women folk of the neighbouring area. It is a self organised group of the lady teachers of North Kamrup College aiming to be engaged in the social activities in terms of upliftment of the women-folk, gender equality of the backward areas.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Mrs. T. Bezbarua</li>
            </ul>
            <p></p>
            <center className="hidden-sm">· · ·</center>
            <h2 id="study-centers">Study Centers</h2>
            <h3>English Study Center</h3>
            <p>English study center was started at North Kamrup College, Baghmara years back in 2003 with a view to teaching English to the students of this college and neighbouring institutions. Since then it has been running succesfully with full co-operation of the college administration and the teaching staff of English department as well as others. It issues certificates of proficiency to the successful candidates after holding examinations at the end of the three month course.</p>
            <h3>Center for Traditional Art &amp; Culture of Bodos</h3>
            <p>The center provides the students of Semester I, T. D. C. the career oriented course on Traditional Art &amp; Culture of Bodos.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Mr. S. R. Basumatary</li>
            </ul>
            <p></p>
            <h3>Information &amp; Career Guidance Cell</h3>
            <p>This cell helps students providing information about the career development and employment. Special classes, seminars and career counselling are conducted by experts from different fields.</p>
            <ul className="li-unstyled">
              <li>Co-ordinator : Mr. A. Das</li>
            </ul>
            <p></p>
            <h3>Research &amp; Extension Education Cell</h3>
            <p>Research journal and News bulletin are annually published by this cell to focus on the research activities of teachers and extension education services are provided to the neighbouring communities in respect of Health, Drinking water, Superstitious belief, Illiteracy, etc.</p>
            <ul className="li-unstyled">
              <li>Convenor : Dr. J. C. Kalita</li>
            </ul>
            <p></p>
            <center className="hidden-sm">· · ·</center>
            <h2 id="national-service-scheme">National Service Scheme (N. S. S.)</h2>
            <p>For the career and social involvement and to develop the leadership characteristics and social responsibility the students may get associated with National Service Scheme (N. S. S.).</p>
          </div>
        </div>
      </div>
    </main>
    <div className="spacer"></div>
  </div>
)

export default Facilities



